.modal {
  text-align: center;
}
.modal:before {
	display: inline-block;
	vertical-align: middle;
	content: " ";
	height: 100%;
}
.modal-dialog {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  width: 100%;
  @include media-breakpoint-down(sm) {
  	max-width: 400px;
  }
  @include media-breakpoint-down(xs) {
  	max-width: 320px;
  }
}